import React, { PureComponent } from "react";
import { GET_MEDIA_NAME } from "../../../../utils";
import AuthorAvatar from "./Elements/AuthorAvatar";


export default class Author extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      isAuthorImagevalid: true
    }
  }
  render() {
    const { author, authorClass, network } = this.props;

    return <div key={Math.random()} className={authorClass}>
      {!this.state.isAuthorImagevalid ?
        <AuthorAvatar authorClass={authorClass} username={author.name} network={network} color={network.id === null ? `#000` : network.color} errorPic={author.errorPic} />
        :
        <img key={Math.random()} className={`${authorClass}__`} src={author.picture && String(author.picture).includes('author.png') ? " " : author.picture.replace(/[ ]+/g, "")} alt={GET_MEDIA_NAME(author.picture)} width={44} height={44}
          onError={(e) => {
            const background = network.color == '#ffffff' ? '#000' : network.color
            e.target.src = author.errorPic ? author.errorPic : `https://ui-avatars.com/api/?name=${author.name.replace(/\s/g, '')}&background=${String(network.id === null ? `#000` : background).replace("#", '')}&color=fff&length=1`
            e.target.alt = author.name;
            // this.setState({
            //   isAuthorImagevalid: false
            // })
          }}
        />
      }
    </div>
  }
}

